/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from 'react';
import { sentenceCase, capitalCase } from 'change-case';

// @mui
import { useTheme } from '@mui/material/styles';
import {
    Card,
    Table,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Grid,
    Stack,
    Tabs,
    Tab
} from '@mui/material';

// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number
import { fCurrency } from '../../utils/formatNumber';
// sections
import { TransHead, PlanToolbar } from '../../sections/admin/user/list';
// axios
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
    { id: 'reference', label: 'Reference', alignRight: false },
    { id: 'type', label: 'Type', alignRight: false },
    { id: 'amount', label: 'Amount', alignRight: false },
    { id: 'oldbal', label: 'Old Balance', alignRight: false },
    { id: 'newbal', label: 'New Balance', alignRight: false },
    { id: 'date', label: 'Date', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
];

const WALLET_TABS = [
    { value: 'all', label: 'All' },
    { value: 'deposits', label: 'Deposits' },
    { value: 'payments', label: 'Payments' }
];

// ----------------------------------------------------------------------

export default function WalletSummary() {
    const theme = useTheme();
    const { themeStretch } = useSettings();
    const [transactions, setTransactions] = useState([]);
    const [page, setPage] = useState(0);
    const [load, SetLoad] = useState(true);
    const [totalPage, SetTotal] = useState(0);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [isNotFound, SetNotFound] = useState(false);
    const [activeTab, setActiveTab] = useState('all');
    const [summary, setSummary] = useState({
        totalDeposits: 0,
        totalPayments: 0,
        currentBalance: 0
    });
    const AccessToken = window.localStorage.getItem('accessToken');

    useEffect(() => {
        initialize(page, rowsPerPage, filterName, activeTab);
        fetchSummary();
    }, []);

    const fetchSummary = async () => {
        try {
            // Fetch deposit summary
            const depositRes = await axios.get(
                `/api/system/all/deposit/trans/habukhan/${AccessToken}/secure?page=1&limit=1000&status=ALL&search=`
            );
            const deposits = depositRes.data?.deposit_trans?.data || [];
            const totalDeposits = deposits.reduce((sum, item) => sum + parseFloat(item.amount || 0), 0);

            // Fetch transaction summary for payments
            const transRes = await axios.get(
                `/api/system/all/history/records/${AccessToken}/secure?page=1&limit=1000&status=ALL&search=`
            );
            const payments = transRes.data?.all_summary?.data || [];
            const totalPayments = payments.reduce((sum, item) => sum + parseFloat(item.amount || 0), 0);

            setSummary({
                totalDeposits,
                totalPayments,
                currentBalance: totalDeposits - totalPayments
            });
        } catch (error) {
            console.error('Error fetching summary:', error);
        }
    };

    const initialize = async (pag, Habukhan = 10, search, tab) => {
        const Habukhan_page = pag + 1;
        SetLoad(true);

        try {
            let allTransactions = [];

            if (tab === 'all' || tab === 'deposits') {
                // Fetch deposits
                const depositRes = await axios.get(
                    `/api/system/all/deposit/trans/habukhan/${AccessToken}/secure?page=${Habukhan_page}&limit=${Habukhan}&status=ALL&search=${search}`
                );
                const deposits = (depositRes.data?.deposit_trans?.data || []).map(item => ({
                    ...item,
                    type: 'Deposit',
                    transType: 'deposit'
                }));
                allTransactions = [...allTransactions, ...deposits];
            }

            if (tab === 'all' || tab === 'payments') {
                // Fetch payments (transaction history)
                const transRes = await axios.get(
                    `/api/system/all/history/records/${AccessToken}/secure?page=${Habukhan_page}&limit=${Habukhan}&status=ALL&search=${search}`
                );
                const payments = (transRes.data?.all_summary?.data || []).map(item => ({
                    ...item,
                    type: 'Payment',
                    transType: 'payment'
                }));
                allTransactions = [...allTransactions, ...payments];
            }

            // Sort by date
            allTransactions.sort((a, b) => new Date(b.plan_date || b.date) - new Date(a.plan_date || a.date));

            setTransactions(allTransactions);
            SetTotal(allTransactions.length);
            SetLoad(false);
            setPage(pag);
            SetNotFound(allTransactions.length === 0);
        } catch (error) {
            console.error('Error fetching wallet data:', error);
            SetLoad(false);
            setTransactions([]);
            SetNotFound(true);
        }
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        initialize(0, event.target.value, filterName, activeTab);
        setPage(0);
    };

    const handleFilterByName = async (filterName) => {
        setFilterName(filterName);
        setPage(0);
        initialize(page, rowsPerPage, filterName, activeTab);
    };

    const handleTabChange = (event, newValue) => {
        setActiveTab(newValue);
        setPage(0);
        initialize(0, rowsPerPage, filterName, newValue);
    };

    return (
        <Page title="Wallet Summary">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Wallet Summary"
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
                        { name: 'Wallet Summary' },
                    ]}
                />

                {/* Summary Cards */}
                <Grid container spacing={3} sx={{ mb: 3 }}>
                    <Grid item xs={12} md={4}>
                        <Card sx={{ p: 3, textAlign: 'center' }}>
                            <Typography variant="subtitle2" color="text.secondary">
                                Total Deposits
                            </Typography>
                            <Typography variant="h3" color="success.main">
                                ₦{fCurrency(summary.totalDeposits)}
                            </Typography>
                        </Card>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <Card sx={{ p: 3, textAlign: 'center' }}>
                            <Typography variant="subtitle2" color="text.secondary">
                                Total Payments
                            </Typography>
                            <Typography variant="h3" color="error.main">
                                ₦{fCurrency(summary.totalPayments)}
                            </Typography>
                        </Card>
                    </Grid>

                    <Grid item xs={12} md={4}>
                        <Card sx={{ p: 3, textAlign: 'center' }}>
                            <Typography variant="subtitle2" color="text.secondary">
                                Current Balance
                            </Typography>
                            <Typography variant="h3" color="primary.main">
                                ₦{fCurrency(summary.currentBalance)}
                            </Typography>
                        </Card>
                    </Grid>
                </Grid>

                {/* Transactions Table */}
                <Card>
                    <Tabs
                        value={activeTab}
                        onChange={handleTabChange}
                        scrollButtons="auto"
                        variant="scrollable"
                        allowScrollButtonsMobile
                    >
                        {WALLET_TABS.map((tab) => (
                            <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
                        ))}
                    </Tabs>

                    <PlanToolbar filterName={filterName} onFilterName={handleFilterByName} />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table>
                                <TransHead
                                    order={'desc'}
                                    orderBy={'date'}
                                    headLabel={TABLE_HEAD}
                                    rowCount={transactions.length}
                                />

                                {!load ? (
                                    <TableBody>
                                        {transactions.map((row, index) => {
                                            const {
                                                transid,
                                                reference,
                                                type,
                                                amount,
                                                oldbal,
                                                newbal,
                                                plan_date,
                                                date,
                                                plan_status,
                                                status,
                                                transType
                                            } = row;

                                            const displayDate = plan_date || date;
                                            const displayStatus = plan_status !== undefined ? plan_status : status;

                                            return (
                                                <TableRow hover key={transid || reference || index} tabIndex={-1}>
                                                    <TableCell>
                                                        <Typography variant="subtitle2" noWrap>
                                                            {transid || reference || 'N/A'}
                                                        </Typography>
                                                    </TableCell>
                                                    <TableCell align="left">
                                                        <Label
                                                            variant="soft"
                                                            color={transType === 'deposit' ? 'success' : 'info'}
                                                        >
                                                            {type}
                                                        </Label>
                                                    </TableCell>
                                                    <TableCell align="left">
                                                        <Typography
                                                            variant="subtitle2"
                                                            color={transType === 'deposit' ? 'success.main' : 'error.main'}
                                                        >
                                                            {transType === 'deposit' ? '+' : '-'}₦{fCurrency(amount)}
                                                        </Typography>
                                                    </TableCell>
                                                    <TableCell align="left">₦{fCurrency(oldbal)}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(newbal)}</TableCell>
                                                    <TableCell align="left">{displayDate}</TableCell>
                                                    <TableCell align="left">
                                                        <Label
                                                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                            color={displayStatus === 1 ? 'success' : displayStatus === 0 ? 'warning' : 'error'}
                                                        >
                                                            {sentenceCase(
                                                                (displayStatus === 0 && 'processing') ||
                                                                (displayStatus === 1 && 'successful') ||
                                                                (displayStatus === 2 && 'failed') ||
                                                                'completed'
                                                            )}
                                                        </Label>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                ) : (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={7} sx={{ py: 3 }}>
                                                <Typography variant="body2">Loading ...</Typography>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}

                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={7} sx={{ py: 3 }}>
                                                <SearchNotFound searchQuery={filterName} />
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25, 100]}
                        component="div"
                        count={totalPage}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={(e, page) => initialize(page, rowsPerPage, filterName, activeTab)}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </Card>
            </Container>
        </Page>
    );
}
